\">{{ $t('gender.male') }}</el-radio-button>\n            <el-radio-button\n              label=\"other\">{{ $t('gender.other') }}</el-radio-button>\n            <el-radio-button\n              label=\"prefer not to say\">{{ $t('gender.prefernottosay') }}</el-radio-button>\n        </el-radio-group></div>\n      </el-form-item>-->\n\n      <el-form-item\n        :label=\"$t('signup.label.email')\"\n        prop=\"email\">\n        <el-input\n          v-model=\"signUpForm.email\"/>\n      </el-form-item>\n      <el-form-item\n        :label=\"$t('signup.label.password')\"\n        prop=\"password\">\n        <div id=\"pw\">\n          <password\n            v-model=\"signUpForm.password\"\n            :toggle=\"true\"\n            class=\"input\"\n            default-class=\"el-input__inner\"\n            @feedback=\"showFeedback\"/>\n        </div>\n      </el-form-item>\n      <el-form-item\n        :label=\"$t('signup.label.confirmPassword')\"\n        prop=\"checkPass\">\n        <el-input\n          v-model=\"signUpForm.checkPass\"\n          type=\"password\"/>\n      </el-form-item>\n    </el-form>\n    <el-button\n      type=\"primary\"\n      icon=\"el-icon-arrow-right\"\n      @click.prevent=\"submitForm\">{{ $t('options.signup') }}</el-button>\n    <el-button\n      type=\"text\"\n      icon=\"el-icon-close\"\n      @click.prevent=\"resetForm\">{{ $t('options.reset') }}</el-button>\n\n    <div style=\"margin: 20px;\">\n      <h5>Du bist bereits dabei? <a href=\"drops/auth/login\">Log In</a></h5>\n    </div>\n  </div>\n</template>\n\n<script>\n  import Vue from 'vue'\n import Password from 'vue-password-strength-meter';\n import axios from 'axios'\n import VueAxios from 'vue-axios'\n  import {\n    Button,\n    DatePicker,\n    Form,\n    FormItem,\n    Input\n  } from 'element-ui'\n\n Vue.use(VueAxios, axios);\n  Vue.use(Button);\n  Vue.use(DatePicker);\n  Vue.use(Form);\n  Vue.use(FormItem);\n  Vue.use(Input);\n\n //var axios = require('axios');\n\n\n ex